* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  max-width: 100vw;
  max-height: 100vh;
  background-color: #8ec5fc;
  background-image: linear-gradient(62deg, #8ec5fc 0%, #e0c3fc 100%);
  margin: 0;
  padding: 0 20px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.body--main {
  width: 100vw;
  height: 100vh;
  justify-content: center;
  display: flex;
}

img {
  max-width: 100%;
  display: block;
}

.main-nav {
  padding-top: 10%;
}

.color-switcher {
  justify-content: center;
  padding-top: 20vh;
  display: flex;
}

.container--timer {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0 20px;
}

.task-navigation {
  padding-bottom: 10px;
  position: absolute;
  top: 7px;
  left: 7px;
}

.timer__button {
  color: #000;
  cursor: pointer;
  text-align: center;
  background-color: #85ffbd;
  background-image: linear-gradient(45deg, #85ffbd 0%, #fffb7d 100%);
  border-radius: 10px;
  padding: 15px 45px;
  font-weight: 600;
  transition: box-shadow .25s ease-in-out, background-image .25s ease-in-out;
  box-shadow: 0 5px 9px #d414431a;
}

.timer__button:hover, .timer__button:focus {
  background-image: linear-gradient(-45deg, #85ffbd 0%, #fffb7d 100%);
  box-shadow: 0 8px 12px #8314301a;
}

.timer__button:disabled {
  color: #968989;
  background-image: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

img {
  display: block;
  overflow: hidden;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

button {
  cursor: pointer;
}

.timer {
  width: 100%;
  height: 100vh;
  text-align: center;
}

.timer__tuning {
  padding: 60px 0;
}

.timer__date-input {
  text-align: center;
  background-color: #85ffbd;
  background-image: linear-gradient(45deg, #85ffbd 0%, #fffb7d 100%);
  border-radius: 5px;
  font-size: 20px;
  line-height: 100%;
  transition: background-image .25s ease-in-out;
}

.timer__date-input:hover, .timer__date-input:focus {
  background-image: linear-gradient(-45deg, #85ffbd 0%, #fffb7d 100%);
}

.timer__button {
  font-size: 30px;
  position: absolute;
  bottom: 10%;
  left: 50%;
  transform: translate(-50%);
}

.timer__numbers {
  justify-content: space-around;
  align-items: center;
  padding-bottom: 40px;
  display: flex;
}

.timer__field {
  width: 25%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  overflow: hidden;
}

.timer__field:after {
  content: "";
  z-index: 9999;
  width: 60%;
  height: 60%;
  background-position: center;
  background-size: 102%;
  border-radius: 50%;
  position: absolute;
  top: 2%;
  overflow: hidden;
  box-shadow: 0 5.26027px 19.726px #99721740;
}

.timer__value {
  font-size: 40px;
  font-weight: 900;
}

.timer__label {
  font-style: italic;
}

@media screen and (min-width: 768px) {
  .timer__field:after {
    width: 30%;
    height: 65%;
  }
}

@media screen and (min-width: 1200px) {
  .timer__field:after {
    width: 20%;
    height: 65%;
  }
}

